module.exports = "#!/bin/sh\n\nset -euf\nmkdir -p \"$2/.ssh\"\ncd \"$2/.ssh\"\n\nchown $1 .\n\ntouch authorized_keys 2> /dev/null || true\nchown $1 authorized_keys 2> /dev/null || true\n\nsed -i -e '$a\\' authorized_keys\ncat >> authorized_keys\n\nchown $1 authorized_keys 2> /dev/null || true\nchmod 600 authorized_keys\n"