module.exports = "#!/bin/sh\n\ndir=$(mktemp -d)\ncd \"$dir\"\n\nprocess()\n{\n    if [ -z \"$1\" ]; then\n        return\n    fi\n    case \"$1\" in\n    \\#*)\n        ;;\n    *)\n        echo \"$1\"> authorized_keys\n        echo \"$(LC_ALL=C ssh-keygen -l -f authorized_keys)\"\n        echo \"$1\"\n        ;;\n    esac\n}\n\nsed -e '$a\\' | while read -r line; do\n    process \"$line\"\ndone\n\nrm -f \"$dir/authorized_keys\"\nrmdir \"$dir\"\n"